.su{
    height: 100%;
    width: 100%;
    /* background-color: blue; */
    text-align: center;
    align-items: center;
    padding: 10px;
}

.suc{
    height: 50%;
    width: 30%;
    border: 1px solid blue;
    border-radius: 10px;
    box-shadow: 0 14px 28px rgba(0,0,0,0.25),
    0 10px 10px rgba(0,0,0,0.22);
    transition: 1s;
}

.suc:hover{
    border: 0.5px solid red;
}

.sutext{
    font-size: 30px;
    font-family:'Courier New', Courier, monospace;
    font-weight: 500;
    color: rgb(76, 74, 74);
    text-decoration: underline;
    padding-top: 20px;
    padding-bottom: 20px;
}

.row{
    width: 50%;
    padding: 20px;
}

button{
    padding: 17px 30px;
    border: none;
    background-color: rgb(24, 37, 55);
    color: rgb(233, 81, 81);
    font-size: 18px;
    font-weight: 600;
    border: none;
    margin: 30px 10px 0 0;
    border-radius: 3px ;
    cursor: pointer;
    box-shadow: 0 24px 36px -11px rgba(0 0 0 /9%);
    
}

button i{
    margin-left: 20px ;
}

button:hover{
    box-shadow: none;
}

.rowtext{
    font-family: 'Times New Roman', Times, serif;
    color: rgb(96, 84, 84);
    padding: 10px;
}

.rowtext1{
    color: rgb(60, 60, 93);
}

.button{
    text-align: center;
    padding: 20px;
}

