

.faculty{
    width: 90%;
    padding: 45px;
}

.facultyTag{
    text-align: center;
    font-size: 22px;
    text-decoration: underline;
    font-weight: 400;
}


.princ{
    text-align: center;
    align-items: center;
    padding-top: 80px;
}

.headImg{
    width: 200px;
    height: 200px;
    border-radius: 15px;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16),
    0 3px 6px rgba(0, 0, 0, 0.23);
    transform: translate(0px , -8px);
    border: 0.2px solid black;

}

.headImg:hover{
    width: 300px;
    height: 300px;
    transition: 1s all ease;
    cursor: pointer;
}

