/* ---------------------------Style-------------------- */

.experiment{
    margin: 0;
    font-family: sans-serif;
    background: #f2f2f2;
}

.experimentTag{
    text-decoration: underline;
    text-align: center;
    padding-top: 20px;
    padding-bottom: 50px;
    color: rgb(90, 82, 82);
}

.experimentT{
    text-align: center;
    font-size: 22px;
    margin: 0;
    padding-top: 10px;
}

.experimentList{
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    justify-content: center;
    align-items: center;
    margin: 50px 0;
}

.experimentItem{
    width: 20%;
    margin: 15px;
    box-sizing: border-box;
    float: left;
    text-align: center;
    border-radius: 20px;
    cursor: pointer;
    padding-top: 10px;
    box-shadow: 0 14px 28px rgba(0,0,0,0.25),
    0 10px 10px rgba(0,0,0,0.22);
    transition: 0.5s;
    background-color: #f2f2f2;
}

.experimentItem:hover{
    box-shadow: 0 3px 6px rgba(0,0,0,0.16),
    0 3px 6px rgba(0,0,0,0.23);
    transform: translate(0px , -8px);
    border: 0.2px solid black;
}

.experimentImg{
    width: 200px;
    height: 200px;
    text-align: center;
    margin:  0 auto;
    display: block;
    /* border: 0.5px solid red; */
    border-radius: 20px;
}

.expUl{
    list-style: none;
    display: flex; 
    justify-content: center;
    align-items: center; 
    padding-top: 0;
    /* text-align: center; */
    margin-right: 50px;
    margin-bottom: 10px;
}

.expUlList{
    padding-top: 5px;
}

.fa{
    font-size: 26px;
    transition: 0.4s;
    margin: 3px;
}

.checked{
    color: rgb(207, 30, 30);
}

.fa:hover{
    transform: scale(1.3);
    transition: 0.6s;
}

.expLink{
    text-decoration: none;
    color: rgb(78, 68, 68);
}

