
.home{
    width: 100%;
    position: relative;
    display: flex; 
    align-items: center;
    justify-content: center;
    margin: auto;

    .overlay{
        position: absolute;
        height: 100%;
        width: 100% ;
        background-color: rgb(47, 106, 127);
        top: 0;
        bottom: 0;
        z-index: 1;
        mix-blend-mode: hard-light;
    }


    video{
        position: absolute;
        height: 100%;
        width: 100%;
        top: 0;
        bottom: 0; 
        object-fit: cover;
    }


    .homeContent{
        width: 100%;
        height: max-content;
        padding: 8rem 1.5rem 3rem;
        display: flex;
        row-gap: 3rem;
        flex-direction: column;
        align-items: stretch;
        justify-content: center;
        margin: auto;
        color: white;
        z-index: 100;


        .textDiv{
            padding: 2rem 0;


            .smallerText{
                font-size: 13px;
                text-transform: uppercase;
            }

            .smallerText1{
                font-size: 20px;
                text-transform: uppercase;
                font-weight: 400;
            }

            .homeTitle{
                position: relative;
                font-size: 1.8rem;
            }
        
        }

        .cardDiv{
            position: relative;
            padding: 2rem 1rem;
            background-color: rgb(150, 225, 234);
            opacity: 0.7;
            border-radius: 10px;
            gap: 1rem;


            label{
                display: block;
                color: black;
                padding-bottom: 10px;
                font-size: 23px;
                font-weight: 500;
            }


            .destinationInput{
                display: flex;
                justify-content: space-between;
                align-items: center;
                text-align: center;


            }

            .input{
                background: red;
                padding: 10px;
                border-radius: 5px;


                &:hover{
                    background: blue;
                    cursor: pointer;
                }
            }

        }

        .inquiry{
            position: absolute;
            background: blue;
            width: max-content;
            bottom: -0.7rem;
            column-gap: .8rem;
            padding: 5px 1rem;
            border-radius: 3rem;
            left: 50%;
            transform: translate(-50%);
            justify-content: center;

            &:hover{
                background: red;
                cursor: pointer;
            }
        }

        .su{
            height: 30%;
            width: 30%;
            text-align: center;
            align-items: center;
        }
        
    }
  

}








